.program {
  padding: 30px 20px 18px;
  background-color: #f2f2f2;
}

.program__title {
  margin: 0;
  margin-bottom: 29px;
  padding-left: 70px;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  color: #000000;
  text-transform: uppercase;
}

.program__description {
  margin: 0;
  margin-bottom: 38px;
}

.program__link {
  display: inline-block;
  padding: 4px 57px 4px 0;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}

@media (min-width: @tablet-width) {
  .program {
    padding: 77px 316px 57px 52px;
  }

  .program__title {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 24px;
    padding-left: 0;
  }

  .program__description {
    color: #000000;
    margin-bottom: 52px;
  }

  .program__link {
    font-size: 20px;
    line-height: normal;
  }
}

@media (min-width: @desktop-width) {
  .program {
    padding: 77px 61px 59px 52px;
  }

  .program__title {
    margin-bottom: 73px;
    padding-left: 162px;
  }

  .program__description {
    margin-bottom: 27px;
  }
}
